<template>
  <page
    @on-ready="onReady"
    ref="page"
    class="myquestion-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
    :pullDownRefresh="true"
    :pullUpLoad="true"
    @on-refresh="onRefresh"
    @on-show="onShow"
    @on-load="onLoad"
  >
    <div slot="header">
      <van-nav-bar id="header" title="我提报的故障" @click-left="onClickLeft" left-arrow></van-nav-bar>
      <div class="search-bar">
        <van-search placeholder="请输入搜索工单编号" v-model="filter.key" />
        <div class="search-action">
          <i class="search-scan search-icon"></i>
          <i class="search-filter search-icon" @click="handleFilter"></i>
        </div>
      </div>
    </div>
    <van-loading type="spinner" v-if="loading" class="page-loading" />
    <div class="myquestion-content">
      <div class="myquestion-item" v-for="item in list" :key="item.id" @click="handleDetail(item)">
        <div class="item-title">
          <span>{{ item.no }}</span>
          <span class="gz">故障</span>
          <span v-if="item.state == 'wait_assigned'" class="item.status" style="color:#ffb105">待受理</span>
          <span v-else-if="item.state == 'assigned'" class="item-status" style="color:#ffb105">待关闭</span>
          <span v-else-if="item.state == 'neglected'" class="item-status" style="color:#ffb105">已忽略</span>
          <span v-else-if="item.state == 'close'" class="item-status" style="color:#ffb105">已关闭</span>
          <span v-else-if="item.state == 'create'" class="item-status">转提报</span>
        </div>
        <div class="item-content">
          <van-icon name="warning" v-if="item.isEmergency" />
          <span class="content"><span class="jj" v-if="item.isEmergency">【紧急】</span>{{ item.content }}</span>
        </div>
        <div class="item-address">
          <van-icon name="location-o" />
          <span class="addr">{{ item.line }}-{{ item.station }}</span>
        </div>
        <div class="item-time">
          <van-icon name="clock-o" />
          <span class="time">通报时间：{{ item.createTime }}</span>
          <span class="y-time">{{ item.timeConsuming }}min</span>
        </div>
      </div>
    </div>

    <template slot="bar">
      <van-popup v-model="filterShow" position="right" class="order-filter-popup" :lock-scroll="false">
        <a-scroll scrollbar ref="filterScroller" v-if="filterShow" class="order-filter-scroll">
          <div class="order-filter-content">
            <van-cell-group title="线别站点" :border="false">
              <van-button type="default" class="xbzd" @click="handleSelectLine">
                {{ line && line.name }} / {{ station && station.name }}
              </van-button>
            </van-cell-group>
            <van-cell-group title="状态筛选" :border="false">
              <van-row gutter="10" class="filter-options">
                <van-col span="8" v-for="(value, key) in falutStatusEnum" :key="key">
                  <div
                    class="filter-options-item"
                    :class="{ 'is-active': tempFilter.status.indexOf(key) >= 0 }"
                    @click="handleToggleStatus(key)"
                  >
                    {{ value }}
                  </div>
                </van-col>
              </van-row>
            </van-cell-group>

            <van-cell-group title="通报时间" :border="false">
              <van-dropdown-menu class="select-bar">
                <van-dropdown-item :title="filter.startTime" ref="startTime">
                  <van-datetime-picker
                    v-model="startTime"
                    type="date"
                    @cancel="onBeginDateCancel"
                    @confirm="onBeginDateConfirm"
                  />
                </van-dropdown-item>
                <van-dropdown-item :title="filter.endTime" ref="endTime">
                  <van-datetime-picker
                    v-model="endTime"
                    type="date"
                    @cancel="onEndDateCancel"
                    @confirm="onEndDateConfirm"
                  />
                </van-dropdown-item>
              </van-dropdown-menu>
            </van-cell-group>
          </div>
        </a-scroll>

        <div class="order-filter-action">
          <van-button color="#108EE9" @click.native="handleConfirmFilter">确定</van-button>
        </div>
      </van-popup>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import AScroll from '../../components/Scroll'
import navigate from '../../utils/navigate.js'
import io from '../../api'
import db from '../../db'
import { falutStatusEnum } from '../../utils/enum.js'
import moment from 'moment'

export default {
  components: {
    Page,
    AScroll
  },
  data: () => ({
    headerHeightVW: 66 / 3.75,
    header: null,
    filter: {
      id: '',
      key: '',
      status:
        'wait_assigned,assigned,neglected,close,create',
      line: '',
      station: '',
      startTime: moment()
        .startOf('month')
        .format('YYYY-MM-DD'),
      endTime: moment()
        .endOf('month')
        .format('YYYY-MM-DD'),
      pageIndex: 1,
      pageSize: 5
    },
    tempFilter: {},
    filterShow: false,
    falutStatusEnum: falutStatusEnum,
    loading: true,
    list: [],
    localList: [],
    startTime: new Date(),
    endTime: new Date(),
    line: null,
    station: null
  }),
  methods: {
    onReady(header, main) {
      this.header = header
      this.headerHeightVW = ((this.header.h + 60) * 100) / this.header.w
      localStorage.setItem('address_select', '')
      const user = localStorage.getItem('user')
      this.filter.id = JSON.parse(user).id
      // this.onRefresh()
      this.loadLocalData()

      setTimeout(() => {
        navigate.back('createFault')
      }, 600)
    },
    onClickLeft() {
      navigate.back()
    },
    onShow() {
      if (localStorage.getItem('address_select')) {
        const address = JSON.parse(localStorage.getItem('address_select'))
        this.line = address[0]
        this.station = address[address.length - 1]

        localStorage.setItem('address_select', '')
      }
      // this.onRefresh()
      this.loadLocalData()
    },
    loadLocalData() {
      this.localList = []
      db.getMyFault().then(data => {
        this.localList = data

        this.onRefresh()
      })
    },
    onRefresh() {
      this.filter.pageIndex = 1
      io.getMyFault(this.filter)
        .then(data => {
          this.list = [...this.localList, ...data.list]
          this.filter.pageIndex++
          this.$refs.page.finishRefresh()
          this.loading = false
        })
        .catch(e => {
          this.$toast(e)
          this.$refs.page.finishRefresh()
          this.loading = false
          this.list = this.localList
        })
    },
    onLoad() {
      io.getMyFault(this.filter)
        .then(data => {
          this.list = this.list.concat(data.list)
          this.filter.pageIndex++
          if (data.list.length == 0) {
            this.$refs.page.endLoad()
          } else {
            this.$refs.page.updateLoad()
          }
          this.loading = false
        })
        .catch(e => {
          this.$toast(e)
          this.$refs.page.updateLoad()
          this.loading = false
        })
    },
    handleFilter() {
      this.tempFilter = { ...this.filter }
      this.filterShow = true
    },
    handleConfirmFilter() {
      this.filterShow = false
      this.onRefresh()
    },
    handleDetail(item) {
      if (item.state == 'create') {
        navigate.open('createFault', { detail: item.id })
      } else {
        navigate.open('faultdetail', { id: item.id })
      }
    },
    handleToggleStatus(key) {
      let typeArray = this.tempFilter.status.split(',')
      if (this.tempFilter.status.indexOf(key) >= 0) {
        typeArray.splice(
          typeArray.findIndex(item => item == key),
          1
        )
      } else {
        typeArray.push(key)
      }
      this.tempFilter.status = typeArray.join(',')


      this.filter.status = this.tempFilter.status
    },
    handleSelectLine() {
      navigate.open('address')
    },
    onBeginDateCancel() {
      this.$refs.startTime.toggle()
    },
    onBeginDateConfirm(val) {
      this.filter.startTime = moment(val).format('YYYY-MM-DD')
      this.$refs.startTime.toggle()
      this.onRefresh()
    },
    onEndDateCancel() {
      this.$refs.endTime.toggle()
    },
    onEndDateConfirm(val) {
      this.filter.endTime = moment(val).format('YYYY-MM-DD')
      this.$refs.endTime.toggle()
      this.onRefresh()
    }
  },
  watch: {
    tabActive(val) {
      this.loading = true
      if (val == 0) {
        this.filter.status =
          'assigned,pending_sign,waiting_repair,pending_submission,pending_item_review,pending_review,close,sold_item,hang'
        this.filter.pageIndex = 1
      }
      if (val == 1) {
        this.filter.status = 'assigned'
        this.filter.pageIndex = 1
      }
      if (val == 3) {
        this.filter.status = 'pending_item_review,pending_review'
        this.filter.pageIndex = 1
      }
      if (val == 4) {
        this.filter.status = 'hang'
        this.filter.pageIndex = 1
      }
      if (val == 5) {
        this.filter.status = 'close,sold_item'
        this.filter.pageIndex = 1
      }
      this.$nextTick(() => {
        this.onRefresh()
      })
    }
  }
}
</script>

